  font-weight: 600;
  text-transform: uppercase;
  color: #0F3D33;
  height: 34px;
  background: transparent;
  left: 9px;
  top: 4px;
  line-height: 34px;
}
.pricing-area-l-17 .btn-toggle-2:after {
  font-family: Inter;
  position: absolute;
  content: "Yearly";
  width: 46%;
  font-size: 12px;
  letter-spacing: -0.3px;
  font-weight: 600;
  text-transform: uppercase;
